.resultPage {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: #fff;
  z-index: 99;
  text-align: center;
}

.resultPage .result {
  font-size: 1.6rem;
  color: #108ee9;
  margin: 10vh;
}

.resultPage .item {
  height: 10vh;
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
}

.resultPage .see {
  font-size: 0.48rem;
  width: 1.333333rem;
  display: inline-block;
  text-align: center;
}

.resultPage .price-label {
  font-size: 0.426667rem;
}

.resultPage .price {
  font-size: 1.066667rem;
  font-weight: bolder;
  margin-right: 0.266667rem;
}