.parts-result .result-title {
  font-size: 0.48rem;
  margin: 0.266667rem 0;
}

.parts-result .am-list-item .am-list-line .am-list-extra {
  flex-basis: auto !important;
}

.parts-result .swipe-wrap {
  position: relative;
}

.parts-result .swipe-wrap .iconfont {
  color: #108ee9;
  padding: 0 0.133333rem;
  position: relative;
}

.parts-result .swipe-wrap .iconfont:after {
  position: absolute;
  content: ' ';
  top: -0.133333rem;
  right: -0.133333rem;
  left: -0.133333rem;
  bottom: -0.133333rem;
}

.parts-result .swipe-wrap.active .swipe-cover {
  left: -1.6rem;
}

.parts-result .swipe-wrap .swipe-cover {
  position: relative;
  transition: all 0.25s;
  left: 0;
  z-index: 10;
}

.parts-result .swipe-wrap .swipe-content {
  z-index: 1;
  background-color: red;
  color: white;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  width: 1.6rem;
  display: flex;
  align-items: center;
  margin: 0.026667rem;
}

.parts-result .swipe-wrap .swipe-content div {
  text-align: center;
  flex: 1 1;
}

.modal-list .am-list-body::before {
  display: none !important;
}